import 'list.scss';
import '_lightbox';
import '_header';



$(document).ready(function() {


    //--------------------- imagesLoaded ---------------------//
    function removeLoading() {
        $(".loading").fadeOut(function() {
            $(this).remove();
            lazyload();
        });
    }

    imagesLoaded("html, body", function() {
        removeLoading();
    });
});